#select-area{
    width: 100%;
    height: 100vh;
    background-color: rgb(245, 13, 245);
    text-align: center;

    .select-area_hello{
        font-size: 3rem;
        color: white;
        text-shadow: 1px 1px 2px black;
        opacity: 0;
    }
    
    .select-area_hello{
        font-size: 3rem;
        color: white;
        text-shadow: 1px 1px 2px black;
        opacity: 0;
    }

    .select-area_title{
        font-size: 3rem;
        color: white;
        text-shadow: 1px 1px 2px black;
        text-transform: uppercase;
        font-weight: bold;
        margin-top: 30px;
    }

    .select-area_item{
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        /* background-color: red; */
        width: 100%;
        height: 25vh;
        position: absolute;
        top: 55%;
        left: 50%;
        transform: translate(-50%, -55%);
        z-index: 1;

        li{
            list-style-type: none;
            background: rgb(177, 177, 190);
            font-size: 2rem;
            font-weight: bold;
            font-family: 'Courier New', Courier, monospace;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            box-shadow: .5px .5px 3px black;
            transition: .2s;
        }

        li:hover{
            box-shadow: 0 0 6px black;
            transition: .5s;
        }

        .select-area_item-topic{
            border-radius: 100%;
            height: 100%;
            width: 12%;
            background-color: orange;
            color: rgb(33, 33, 156);
        }
        
        .select-area_item-collection{
            height: 35%;
            border-radius: 20px;
            width: 14%;
            background-color: red;
            color: white;
        }
        
        .select-area_item-content{
            height: 100%;
            width: 12%;
            background-color: blue;
            color: yellow;
        }
    }
}